.spinner {
  height: 120px;
  width: 120px;
  border-radius: 50%;
  animation: cp-round-animate 1s ease-in-out infinite;

  .image {
    height: 120px;
    width: 120px;
  }
}

@keyframes cp-round-animate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}